/** PARTIE APPLICATIONS   *******************/
.btnVP {
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  border-radius: 0px;
  margin: auto 0;
  color: #FFFFFF;
  background-color: #574b6b;
  padding: 10px 25px 10px 25px;
  border: solid #FFFFFF 2px;
  text-decoration: none;
  transform: skewX(-15deg);
  transition: background-color 0.5s ease;
}

.btnVP:hover {
  cursor: pointer;
  background-color: #723753;
}

.circleIconRow {
  box-sizing: border-box;
  min-height: 1px;
  position: relative;
  padding-left: 15px;
  padding-right: 15px;
  width: 85%;
  overflow: hidden;
  margin: auto;
  max-width: 100%;
  right: auto;
  left: auto;
}

.circleIcon {
  background-color: #bbb;
  min-height: 300px;
  width: 300px;
  flex: 0;
  min-width: 300px;
  border-radius: 50%;
  left: 50%;
  -webkit-transform: translateX(-50%) translatey(0%);
  -moz-transform: translateX(-50%) translatey(0%);
  transform: translateX(-50%) translatey(0%);
  box-sizing: border-box;
  position: relative;
  padding-left: 15px;
  padding-right: 15px;
  overflow: hidden;
  max-width: 100%;
  margin-left: 0;
}

#SVGInlineChallenge svg {
  height: 100%;
  width: 100%;
}

#SVGInlineAnalyze svg {
  width: 90%;
}

.colCheckerboardOne {
  background-color: #fbf8f6;
  color: #333333;
}

.colCheckerboardTwo {
  background-color: #333333;
  color: #fbf8f6;
}

.svgIcon1 svg {
  max-height: 100px;
}

@media only screen and (min-device-width: 0px) and (max-device-width: 768px) {
  .retouche {
    transform: translateY(0) !important;
  }
}

@media only screen and (min-device-width: 768px) and (max-device-width: 992px) {
  .retouche {
    transform: translateY(10%) !important;
  }
}

.retouche {
  transform: translateY(25%);
