/** PARTIE INTRANET   *******************/
.rowHomepage {
  background: linear-gradient(to left bottom, #FF5889, #FFBD68) no-repeat;
}

.firstRowIntranet {
  /*filter: grayscale(100%);*/
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("../../assets/images/intranetBg.jpg") center fixed;
  animation-name: img-ani;
  animation-duration: 1s;
  animation-timing-function: ease-in;
}

#SVGIntranet svg {
  height: 100%;
  width: 100%;
  max-height: 145px;
  margin-top: 20px;
}

.secondRowIntranet_firstElem {
  animation-name: img-ani;
  opacity: 0;
  animation-duration: 500ms;
  animation-timing-function: ease-in;
  -webkit-transition-delay: 500ms;
  /* Safari */
  animation-delay: 500ms;
  animation-fill-mode: forwards;
}

.secondRowIntranet_secondElem {
  animation-name: img-ani;
  opacity: 0;
  animation-duration: 500ms;
  animation-timing-function: ease-in;
  -webkit-transition-delay: 1s;
  /* Safari */
  animation-delay: 1s;
  animation-fill-mode: forwards;
}

.secondRowIntranet_thirdElem {
  animation-name: img-ani;
  opacity: 0;
  animation-duration: 500ms;
  animation-timing-function: ease-in;
  -webkit-transition-delay: 1500ms;
  /* Safari */
  animation-delay: 1500ms;
  animation-fill-mode: forwards;
}

.secondRowIntranet_firstElem:hover .card-body *, .secondRowIntranet_secondElem:hover .card-body *, .secondRowIntranet_thirdElem:hover .card-body * {
  color: black !important;
}

.secondRowIntranet_firstElem .card-body *, .secondRowIntranet_secondElem .card-body *, .secondRowIntranet_thirdElem .card-body * {
  -webkit-transition: color 250ms;
  transition: color 250ms;
}

.secondRowIntranet_firstElem:hover svg, .secondRowIntranet_secondElem:hover svg, .secondRowIntranet_thirdElem:hover svg {
  fill: black !important;
}

.secondRowIntranet_firstElem svg, .secondRowIntranet_secondElem svg, .secondRowIntranet_thirdElem svg {
  -webkit-transition: fill 250ms;
  transition: fill 250ms;
}

.secondRowIntranet_firstElem:hover, .secondRowIntranet_secondElem:hover, .secondRowIntranet_thirdElem:hover {
  background-color: rgba(255, 255, 255, 0.8) !important;
  cursor: pointer;
}

.secondRowIntranet_firstElem, .secondRowIntranet_secondElem, .secondRowIntranet_thirdElem {
  -webkit-transition: background-color 250ms;
  transition: background-color 250ms;
}

.colBgSecond {
  background: url("../../assets/images/intranet/4391.JPG") no-repeat center;
  height: 100%;
  background-size: cover;
  padding: 0;
}

.colBgFourth {
  background: url("../../assets/images/intranet/personal_trainer_with_client.jpg") no-repeat center;
  height: 100%;
  background-size: cover;
}

